/*  */
 .navigation {
    position: fixed;
    width: 300px;
    height: 100%;
    background: #242424;
    /* background: #003147; */
    transition: 0.5s;
    overflow: hidden;
}
 .navigation.active {
    width: 60px;
}
 .navigation ul {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
}
 .navigation ul li {
    position: relative;
    width: 100%;
    list-style: none;
}
 .navigation ul li:hover {
    background: #eee;
    /* background: #03a9f4; */
}
 .navigation ul li:nth-child(1) {
    margin-bottom: 20px;
}
 .navigation ul li:nth-child(1):hover {
    background: transparent;
    color: #eee;
}
 .navigation ul li:nth-child(1) a:hover {
    background: transparent;
    color: #eee;
}
 .navigation ul li a {
    position: relative;
    width: 100%;
    display: -webkit-flex;
    display: flex;
    text-decoration: none;
    color: #eee;
    -webkit-align-items: center;
            align-items: center;
}
 .navigation ul li a:hover {
    color: #242424;
}
 .navigation ul li a .icon {
    position: relative;
    display: -webkit-flex;
    display: flex;
    min-width: 60px;
    height: 60px;
    text-align: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    /* color: #fff; */
}
 .navigation ul li a .title {
    position: relative;
    display: block;
    padding: 0 10px;
    height: 60px;
    line-height: 60px;
    white-space: nowrap;
}

.Calendar_container {
    background: #f5f5f5;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    min-height: 100vh;
    margin: auto;
    position: absolute;
    width: calc(100% - 300px);
    left: 300px;
}
.Calendar_container .react-calendar {
    background: white;
    border: 1px solid #a0a096;
    font-family: Arial, Helvetica, sans-serif;
    line-height: 1.125em;
    margin: 0 auto;
    z-index: 10;
    width: auto;
}



.Home_container {
    position: relative;
    width: 100%;
}

/*  */
.Home_container .main {
    position: absolute; 
    width: calc(100% - 320px);
    left: 300px;
    min-height: 100vh;
    background: #f5f5f5;
    transition: 0.5s ease-in-out;
}
.Home_container .main.active {
    width: calc(100% - 60px);
    left: 60px;
}
.Home_container .main .topbar {
    width: 100%;
    background: #fff;
    padding: 0 10px;
    height: 60px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}
.Home_container .main .toggle {
    position: relative;
    width: 60px;
    height: 60px;
    font-size: 24px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    color: #111;
}
.Home_container .main .search {
    position: relative;
    width: 400px;
    margin: 0 10px;
}
.Home_container .main .search label {
    position: relative;
    width: 100%;
}
.Home_container .main .search label input {
    width: 100%;
    height: 40px;
    border-radius: 40px;
    padding: 5px 20px;
    outline: none;
    border: 1px solid rgba(0,0,0,0.2);
    padding-left: 35px;
}
.Home_container .main .search label .search_icon {
    position: absolute;
    left: 15px;
    top: 4px;
}
.Home_container .main .user {
    position: relative;
    min-width: 50px;
    height: 50px;
    border-radius: 50px;
    overflow: hidden;
    cursor: pointer;
}
.Home_container .main .user img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
}
