} */
/*  */
.Home_container .details {
    position: relative;
    /* width: calc(100% - 300px); */
    padding: 20px;
    padding-top: 0;
    display: grid;
    grid-template-columns: 2fr 1fr;
    grid-gap: 20px;
}
.Home_container .details .recentNotes, 
.Home_container .details .recentTasks {
    background: #fff;
    padding: 20px;
}
.Home_container .details .cardHeader {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: flex-start;
            align-items: flex-start;
}
.Home_container .details .cardHeader h2 {
    font-weight: 600;
}
.Home_container .details .cardHeader a {
    position: relative;
    padding: 5px 10px;
    background: #242424;
    color: #fff;
    text-decoration: none;
    border-radius: 6px;
}
.Home_container .details table {
    width: 100%;
    border-collapse: collapse;
    margin-top: 10px;
    /* height: 100%; */
}
.Home_container .details .recentTasks table {
    height: 100%;
}
.Home_container .details table thead tr {
    font-weight: 600;
}
.Home_container .details table tr {
    border-bottom: 1px solid rgba(0,0,0,0.1);
}
.Home_container .details table tbody li:last-child {
    border-bottom: none;
}
.Home_container .details  li:hover {
    background: #f9f9f9;
    cursor: pointer;
}
.Home_container .details table tbody li {
    padding: 9px 5px;
}
.Home_container .recentTasks .todo li:last-child {
    text-align: right;
}
.Home_container .details .todo .todo-item {
    position: relative;
    -webkit-align-items: center;
            align-items: center;
    height: 100%;
}
.Home_container .details .todo .todo-item svg {
    margin-right: 20px;
    font-size: 20px;
}
.Home_container .details .todo .todo-item .content {
    text-align: center;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
}
.Home_container .details .todo .completed {
    background: rgba(0,0,0,0.2);
}
.Home_container .details .todo .uncompleted {
    background: #90caf9;
}

.Home_container .details .recentTasks {
    position: relative;
    display: grid;
    min-height: 520px;
}

.Home_container .details .column .title{
    font-weight: 600;
    font-size: 1.5em;
}
.Home_container .details .column {
    background-color: #fff;
}
.Home_container .details .column table tbody tr td:last-child,
.Home_container .details .column table thead tr th:last-child {
    display: none;
}
.Home_container .details .column table thead tr th:first-child {
    text-align: left;
}
.Home_container .details .column .title {
    display: none;
}

.Home_container .todo-item {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}

/* Responsive */
@media (max-width: 992px) {
    .Home_container .navigation {
        left: -300px;
    }
    .Home_container .navigation.active {
        left: 0;
        width: 300px;
        z-index: 100;
    }
    .Home_container .main {
        width: 100%;
        left: 0;
    }
    .Home_container .main.active {
        width: calc(100% - 300px);
        left: 300px;
    }
   
}
@media (max-width: 768px) {
    .Home_container .details {
        grid-template-columns: repeat(1,1fr);
    }
    .Home_container .cardHeader h2 {
        font-weight: 600;
        font-size: 18px;
    }
}
@media (max-width: 480px) {
    .Home_container .main .topbar {
        width: auto;
        background: #fff;
        padding: 0 10px;
        height: 60px;
        display: -webkit-flex;
        display: flex;
        -webkit-justify-content: space-between;
                justify-content: space-between;
        -webkit-align-items: center;
                align-items: center;
    }
    .Home_container .details .recentNotes, .Home_container .details .recentTasks {
        background: #fff;
        padding: 14px;
        /* width: auto; */
    }
    .Home_container .main .search label input {
        width: auto;
    }
    .Home_container .navigation {
        width: 100%;
        z-index: 1000;
        left: -100%;
    }
    .Home_container .navigation.active {
        width: 100%;
        left: 0;
    }
    .Home_container .toggle.active {
        z-index: 10000;
    }
    .Home_container .bars {
        color: #242424;
    }
    .Home_container .bars.active {
        color: #eee;
        right: 0;
        left: auto;
        left: initial;
        position: absolute;
    }
    /* .Home_container .main,
    .Home_container .main.active {
        width: 100%;
        left: 0;
    } */
}

.Home_container .content {
    /* height: 55%; */
    width: calc(100%-300px);
    position: relative;
    
}
.Home_container .content .cards_container {
    padding: 20px;
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-gap: 20px;
    /* width: 75%;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(8em, 1fr));
    grid-template-rows: auto 1fr auto;
    grid-gap: 10px;
    align-items: center;
    justify-content: center; */
}
.Home_container .content .cards_container .cards {
    position: relative;
    background: #fff;
    padding: 20px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    cursor: pointer;
    box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.25);
    -webkit-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.25);
    -moz-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.25);
    text-decoration: none; 
    -webkit-align-items: center; 
            align-items: center;
}
.Home_container .content .cards_container .cards h2{
    /* font-size: 14px; */
    /* color: #999; */
    font-weight: 400;
    text-shadow: 2px 2px 2px #fff;
    /* color: #eee; */
    color: #383838;
}
.Home_container .icons {
    /* height: 25px; */
    /* width: 25px; */
    font-size: 2.5em;
}
.Home_container .content .cards_container .cards.disable {
    /* background: rgba(56,56,56,.2); */
    background: rgba(86, 26, 26, 0.2);
    cursor: not-allowed;
}
@media (max-width: 992px) {
    .Home_container .content .cards_container {
        grid-template-columns: repeat(2, 1fr);
    }
}
@media (max-width: 480px) {
    .Home_container .content .cards_container {
        grid-template-columns: repeat(1, 1fr);
    }
}
