.todo-container {
    max-height: 65vh;
    width: 90%;
    z-index: 10;
    overflow: auto;
}
.todo-container .todo-list {
    list-style: none;
}
.todo-container .todo {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
}
.todo-container .todo-item {
    display: grid;
    grid-template-columns: 1fr 35px;
    -webkit-align-items: center;
            align-items: center;
    color: #fff;
    padding: 0 20px;
    list-style: none;
    width: 100%;
    padding: 0.5rem;
    /* background: #3a4750; */
    background: #383838;
    border-radius: 4px;
    box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.25);
    -webkit-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.25);
    -moz-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.25);
    /* box-shadow: 0 8px 24px rgba(56,56,56, 0.15); */
    /* background: #3a4750; */
    margin: 10px 0;
}
.todo-container .todo-item.completed {
    text-decoration: line-through;
    color: rgba(255,255,255,0.50);
}
.todo-container .todo-item .content {
    display: grid;
    grid-template-columns: 50px 1fr;
    -webkit-align-items: center;
            align-items: center;
}
