    font-size: 15pt;
}
.todo-container .todo-item .trash {
    cursor: pointer;
    font-size: 17pt;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 100%;
    /* width: 85%; */
}
.todo-container .todo-item .trash:hover {
    color: #f6c90e;
}
.ToDo .form {
    position: absolute;
    bottom: 5%;
    right: 5%;
    width: 50px;
    height: 50px;
    background: #fff;
    /* background: #303841; */
    transition: width 0.5s, height 0.5s;
    transition-delay: 0.75s, 0s;
    z-index: 100;
    border-radius: 4px;
    overflow: hidden;
    
}
.ToDo .form.active {
    width: 85%;
    /* height: 350px; */
    height: 130px;
    transition: width 0.5s, height 0.5s;
    transition-delay: 0s, 0.75s;
}
.ToDo .form .toggle {
    position: relative;
    top: 0;
    left: 0;
    width: 100%;
    height: 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: flex-end;
            justify-content: flex-end;
    -webkit-align-items: center;
            align-items: center;
    /* background: #303841; */
    background: #fff;
    transition: 0.5s;
    cursor: pointer;
}
.ToDo .form .toggle.active {
    background: #3a4750;
}
.ToDo .form .toggle::before {
    content: '+';
    font-size: 2em;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 50px;
    height: 50px;
    /* color: #fff; */
    color: #303841;
    font-weight: 400;
    transition: 0.5s;
}
.ToDo .form .toggle.active::before {
    -webkit-transform: rotate(315deg);
            transform: rotate(315deg);
    color: #fff;
}
.ToDo .form .input {
    position:absolute;
    left: 0;
    width: 100%;
    /* height: 80px; */
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
    -webkit-align-items: center;
            align-items: center;
    padding: 19px;
    
}

